/* eslint-disable standard/no-callback-literal */
import axios from 'axios'
export class DwebAudioView {
  fileName = ''
  filereader = new FileReader()
  audioCtx = null
  audioBufferSourceNode = null
  analyser = null
  musicBuffer = null
  gainNode = null
  canvas
  canvasCtx
  drawVisual = 0
  audio = null
  constructor (audio, canvas) {
    this.audioCtx = new (window.AudioContext || window.webkitAudioContext)()
    this.audio = audio
    this.canvas = canvas
    this.canvasCtx = canvas.getContext('2d')
  }

  initAudio () {
    var source = this.audioCtx.createMediaElementSource(this.audio)
    var gainNode = this.audioCtx.createGain()
    source.connect(gainNode)
    gainNode.connect(this.audioCtx.destination)
    this.analyser = this.audioCtx.createAnalyser()
    source.connect(this.analyser)
    this.analyser.connect(this.audioCtx.destination)
    this.analyser.fftSize = 32768 // , 2N32768
    this.draw()
  }

  /**
   * buffer
   * @param file changee.target.files[0] 
   * @param callback (1()2buffer)
   */
  getFileArrayBuffer (src, callback) {
    axios.get(src, {
      responseType: 'blob'
    }).then(res => {
      this.filereader.readAsArrayBuffer(res.data)
      this.filereader.onload = (event) => {
        this.audioCtx.decodeAudioData(event.target.result, (buffer) => {
          this.musicBuffer = buffer
          // callback(true, buffer)
          this.draw()
        })
      }
    })
  }

  /**
   * 
   * @param time 0.1 
   */
  startPlay (time) {
    this.gainNode.gain.value = 0
    this.audioBufferSourceNode = this.audioCtx.createMediaElementSource(this.audio)
    this.audioBufferSourceNode.connect(this.audioCtx.destination)
    this.audioBufferSourceNode.connect(this.analyser)
    this.audioBufferSourceNode.connect(this.gainNode)
    this.audioBufferSourceNode.start(0, time)
  }

  /**
   * 
   * @description https://developer.mozilla.org/zh-CN/docs/Web/API/AnalyserNode
   */
  draw = () => {
    const analyser = this.analyser
    const canvas = this.canvas
    const canvasCtx = this.canvasCtx
    analyser.fftSize = 256
    const bufferLength = analyser.fftSize
    const dataArray = new Uint8Array(bufferLength)
    this.drawVisual = requestAnimationFrame(this.draw)
    analyser.getByteTimeDomainData(dataArray)
    canvasCtx.fillStyle = 'rgb(255,255,255)'
    canvasCtx.fillRect(0, 0, canvas.width, canvas.height)
    canvasCtx.lineWidth = 2
    canvasCtx.strokeStyle = 'red'
    canvasCtx.beginPath()
    var sliceWidth = canvas.width * 1.0 / bufferLength
    // var barWidth = (500 - 2 * bufferLength) / bufferLength * 1.5
    // canvasCtx.fillRect(0, 0, 500, 180)
    // analyser.getByteFrequencyData(dataArray)
    // for (var i = 0; i < bufferLength; i++) {
    //   canvasCtx.beginPath()
    //   canvasCtx.moveTo(4 + 4 * i * barWidth + barWidth / 2, 178 - barWidth / 2)
    //   canvasCtx.lineTo(4 + 4 * i * barWidth + barWidth / 2, 178 - dataArray[i] * 0.65 - barWidth / 2)
    //   canvasCtx.strokeStyle = '#ff4c21'
    //   canvasCtx.stroke()
    // }
    var x = 0
    for (var i = 0; i < bufferLength; i++) {
      var v = dataArray[i] / 128.0
      var y = v * canvas.height / 2
      if (i === 0) {
        canvasCtx.moveTo(x, y)
      } else {
        canvasCtx.lineTo(x, y)
      }
      x += sliceWidth
    }
    canvasCtx.lineTo(canvas.width, canvas.height / 2)
    canvasCtx.stroke()
  }
}
